import React, { Component } from 'react'

import { Link } from 'react-router-dom'
import { Grid, Button, Toast,Modal } from 'antd-mobile'

import { baseurl } from '../../utils/axios'

import styles from './index.module.css'
import {getToken,isAuth,removeToken} from"../../utils/token"
// 导入axios请求
import{getUserMsg,logout} from"../../request/user"
// 菜单数据
const menus = [
  { id: 1, name: '我的收藏', iconfont: 'icon-coll', to: '/favorate' },
  { id: 2, name: '我的出租', iconfont: 'icon-ind', to: '/rent' },
  { id: 3, name: '看房记录', iconfont: 'icon-record' },
  {
    id: 4,
    name: '成为房主',
    iconfont: 'icon-identity'
  },
  { id: 5, name: '个人资料', iconfont: 'icon-myinfo' },
  { id: 6, name: '联系我们', iconfont: 'icon-cust' }
]

// 默认头像
const DEFAULT_AVATAR = baseurl + '/img/profile/avatar.png'
let alert=Modal.alert
export default class Profile extends Component {
    state = {
        userMsg:{}
    }
    // 判断是否登陆
    isLogin = async () => {

        //    获取到token以后,此时是登录的状态
            const { status, body } = await getUserMsg()
            console.log(status,body);
        //   获取数据成功
            if (status === 200) {
                this.setState({
                    userMsg:body
                })
            } else {
        Toast.fail('您还未登录', 1)
        removeToken('token')
        }
    }
    componentDidMount () {
        console.log(getToken('token'));
        console.log(isAuth('token'));
        console.log(this.props);
        this.isLogin()
    }
    // 点击退出
    logout = () => {
        alert('提示', '确定退出吗？', [
          { text: '取消' },
          {
            text: '确定', onPress: async () => {
              let res = await logout();
              console.log(res)
              if (res.status === 200) {
                removeToken('token');
                this.setState({
                  userInfo: {}
                })
              } else {
                this.props.history.push('/login')
              }
            }
          },
        ])
      }
    render () {
    //   获取路由的信息
    const { history } = this.props

    return (
      <div className={styles.root}>
        {/* 个人信息 */}
        <div className={styles.title}>
          <img
            className={styles.bg}
            src={baseurl + '/img/profile/bg.png'}
            alt="背景图"
          />
          <div className={styles.info}>
            <div className={styles.myIcon}>
              <img className={styles.avatar} src={DEFAULT_AVATAR} alt="icon" />
            </div>
            <div className={styles.user}>
              <div className={styles.name}>游客</div>
              {/* 登录后展示： */}
             {isAuth('token')? <>
                <div className={styles.auth}>
                  <span onClick={this.logout}>退出</span>
                </div>
                  <div className={styles.edit} onClick={
                    () => history.push('/rent/add')
                  }>
                    发布房源
                  <span className={styles.arrow}>
                    <i className="iconfont icon-arrow" />
                  </span>
                </div>
                        </> : <div className={styles.edit}>
                                   {/* 未登录展示： */}
                <Button
                  type="primary"
                  size="small"
                  inline
                  onClick={() => history.push('/login')}
                >
                  去登录
                </Button>
              </div>

             }



            </div>
          </div>
        </div>

        {/* 九宫格菜单 */}
        <Grid
          data={menus}
          columnNum={3}
          hasLine={false}
          renderItem={item =>
            item.to ? (
              <Link to={item.to}>
                <div className={styles.menuItem}>
                  <i className={`iconfont ${item.iconfont}`} />
                  <span>{item.name}</span>
                </div>
              </Link>
            ) : (
                <div className={styles.menuItem}>
                  <i className={`iconfont ${item.iconfont}`} />
                  <span>{item.name}</span>
                </div>
              )
          }
        />
        {/* 加入我们 */}
        <div className={styles.ad}>
          <img src={baseurl + '/img/profile/join.png'} alt="" />
        </div>
      </div>
    )
  }
}
